<template>
    <tabla-inmuebles
        :isMostrarSeleccion="true"
        :isMostrarMenuInmuebles="false"
        :ObjFiltrar="{isBtnFiltrar: true, isAutocompleteCliente: sinClienteSeleccionado}"
        :cabecera="cabecera"
        :inmuebles="inmuebles"
        :isCardCabecera="true"
    />
</template>
<script>
import TablaInmuebles from '../Inmueble/TablaInmuebles.vue'
export default {
    props: {
        sinClienteSeleccionado: { type: Boolean, default: false }
    },
    data:()=>({
        cabecera: [
            { value: 'cliente', text: 'Cliente', sortable: false },
            { value: 'clave', text: 'Clave' },
            { value: 'nombre', text: 'Inmueble' },
            { value: 'estado', text: 'Estado' },
            { value: 'municipio', text: 'Municipio' },
            { value: 'localidad', text: 'Localidad' },
            { value: 'direccion', text: 'Direccion', sortable: false },
        ]
    }),
    components: {
        'tabla-inmuebles': TablaInmuebles,
    },
    computed: {
        inmuebles() { return this.$store.getters['inmueble/getInmuebles'] },
        cliente() { return this.$store.getters['cliente/getCliente'] }
    },
    created() {
        this.inicializarInmuebles()
    },
    methods:{
        async inicializarInmuebles(){
            const { mensaje, estado, error } = await this.$store.dispatch('inmueble/listar', this.sinClienteSeleccionado ? null : this.cliente)
            this.$store.commit('alerta/addAlertas', { 'estado': estado, 'mensaje': mensaje, 'error': error })
        }
    }
};
</script>
