<template>
    <v-dialog v-model="dialog" scrollable max-width="400">
        <template v-slot:activator="{ on, attrs }">
        <v-btn v-bind="attrs" v-on="on" color="primary" class="ma-1">
            <v-icon left>mdi-filter-variant</v-icon> Filtros
        </v-btn>
        </template>
        <v-card class="mx-auto">
            <v-card-title>
                <v-btn icon v-if="paso_filtro === 1" @click="dialog=false"><v-icon>mdi-chevron-left</v-icon></v-btn>
                <v-btn icon v-else @click="paso_filtro=1, filtro = -1"><v-icon>mdi-chevron-left</v-icon></v-btn>
                <v-spacer />
                {{ filtro > -1 ? obj_atributo_filtro[filtro].text : "" }}
                <v-spacer />
                <v-btn icon color="primary" @click="cleanAll()"><v-icon>mdi-eraser</v-icon></v-btn>
            </v-card-title>
            <v-divider />
            <v-stepper v-model="paso_filtro" elevation="0">
                <v-stepper-items>
                    <v-stepper-content step="1">
                        <v-list>
                            <v-list-item-group v-model="filtro">
                                <v-list-item v-for="filtro in obj_atributo_filtro" :key="filtro.indice"
                                    @click="paso_filtro = filtro.indice+2"
                                    :disabled="filtro.disabled"
                                >
                                    <template v-slot:default="{active}">
                                        <v-list-item-content>
                                            <v-list-item-title>{{ filtro.text }}</v-list-item-title>
                                            <v-list-item-subtitle v-if="filtro.text==='Cliente'">{{ cliente ? cliente.nombre: "" }}</v-list-item-subtitle>
                                            <v-list-item-subtitle v-if="filtro.text==='Estado'">{{ estado ? estado.nombre: "" }}</v-list-item-subtitle>
                                            <v-list-item-subtitle v-if="filtro.text==='Fecha'">
                                            {{ fecha_menor ? fecha_menor : "" }} {{ fecha_mayor ? fecha_mayor : "" }}
                                            </v-list-item-subtitle>
                                            <v-list-item-subtitle v-if="filtro.text==='Campo'">{{ contacto ? contacto.nombre: "" }}</v-list-item-subtitle>
                                        </v-list-item-content>
                                        <v-list-item-action>
                                            <v-btn :input-value="active" icon><v-icon>mdi-chevron-right</v-icon></v-btn>
                                        </v-list-item-action>
                                    </template>
                                </v-list-item>
                            </v-list-item-group>
                        </v-list>
                    </v-stepper-content>
                    <v-stepper-content step="2">
                        <v-select
                            v-model="cliente"
                            :items="clientes"
                            label="Cliente"
                            item-text="nombre"
                            return-object
                            class="mt-1"
                            outlined
                            dense
                            clearable
                            no-data-text="No se encontraron ningún cliente."
                        />
                    </v-stepper-content>
                    <v-stepper-content step="3">
                        <v-select
                            v-model="estado"
                            :items="estados"
                            label="Estado"
                            item-text="nombre"
                            return-object
                            class="mt-1"
                            outlined
                            dense
                            clearable
                        />
                    </v-stepper-content>
                    <v-stepper-content step="4">
                        <seleccionar-fecha
                            @eventoFecha="setFechas"
                        />
                    </v-stepper-content>
                    <v-stepper-content step="5">
                        <v-select
                            v-model="contacto"
                            label="Usuario en campo"
                            :items="contactos"
                            item-text="nombre"
                            return-object
                            outlined
                        />
                    </v-stepper-content>
                </v-stepper-items>
            </v-stepper>
            <v-card-actions>
                <v-btn block color="primary" @click="emitAplicarFiltroAtributos">Ver trabajos</v-btn>
            </v-card-actions>
        </v-card>
    </v-dialog>
</template>

<script>
import SeleccionarFechas from "../../components/Seleccion/Date.vue"
export default {
    props:{
        obj_atributo_filtro: { type: Array, default:()=>([]) }
    },
    data: () => ({
        dialog: false,
        // opciones_filtrado: [{text: 'Cliente', disabled: false }, {text:'Estado'}, {text: "Fecha"}, { text: 'Campo' }],
        paso_filtro: 1,
        filtro: -1,
        cliente: null,
        contacto: null,
        estado: null,
        fecha_mayor:null,
        fecha_menor:null,
        step : 1
    }),
    components: {
        'seleccionar-fecha': SeleccionarFechas,
    },
    computed: {
        clientes() { return this.$store.getters['cliente/getClientes'] },
        estados() { return this.$store.getters['ubicacion/getEstados'] },
        contactos(){ return this.$store.getters['contacto/getContactos'] }
    },
    methods: {
        setFechas({menorIgualFecha, mayorIgualFecha}){
            this.fecha_mayor = mayorIgualFecha
            this.fecha_menor = menorIgualFecha
            this.cliente = null
            this.estado = null
        },
        async inicializarClientes() {
            await this.$store.dispatch('cliente/obtenerClientes')
        },
        async inicializarEstados() {
            await this.$store.dispatch('ubicacion/obtenerEstados')
        },
        async inicializarContactos(){
            this.$store.dispatch('contacto/recuperarContactos')
        },
        cleanAll(){
            this.cliente=null
            this.estado=null
            this.fecha_mayor=null
            this.fecha_menor=null
            this.contacto=null
        },
        emitAplicarFiltroAtributos(){
            const { cliente, estado, fecha_mayor, fecha_menor, contacto } = this
            const filtro = { cliente, estado, fecha_mayor, fecha_menor, contacto }
            this.$emit('eventAplicarFiltroAtributos', filtro)
        }
    },
    watch: {
        paso_filtro(event){
            if(event === 2){ this.inicializarClientes() }
            if(event === 3){ this.inicializarEstados() }
            if(event === 5){ this.inicializarContactos() }
        }
    }
};
</script>