<template>
    <v-dialog v-model="is_dialogo" width="auto" max-width="500" persistent>
        <template v-slot:activator="{on, attrs}">
            <span v-on="on" v-bind="attrs">Gestionar cargos</span>
        </template>
        <v-card>
            <v-card-title class="grey lighten-2">Estados a cargo de {{usuario.nombre}}
                <v-spacer></v-spacer>
                <v-btn icon color="error" @click="cerrarDialogo()"><v-icon>mdi-close</v-icon></v-btn>
            </v-card-title>
            <v-card-text v-if="estados.length">
                <br>
                <v-select
                    v-model="estados_cargo"
                    :items="estados"
                    item-text="nombre"
                    item-value="id"
                    chips
                    label="Estados"
                    multiple
                    outlined
                    primary
                />
            </v-card-text>
            <v-card-text v-else class="text--primary container">
                <br>
                <span>No hay una lista de estados para asignar al usuario.</span>
            </v-card-text>
            <v-card-actions>
                <v-spacer />
                <v-btn color="error" class="ma-2" outlined @click="cerrarDialogo()">Cancelar</v-btn>
                <v-btn color="success" outlined :disabled="is_disabled" @click="asignarEstados()">Asignar</v-btn>
            </v-card-actions>
        </v-card>
    </v-dialog>
</template>

<script>
export default{
    props:{
        usuario:{ type: Object, default:()=>({}) },
    },
    data:()=> ({
        dialogo: false,
        set_estados_cargo: []
    }),
    created(){
        this.inicializarEstados()
    },
    computed: {
        estados() { return this.$store.getters['ubicacion/getEstados'] },
        is_dialogo: {
            get(){ return this.dialogo },
            set(nuevoValor){
                this.dialogo = nuevoValor
                if(nuevoValor){ this.set_estados_cargo = this.usuario.estados_cargo }
            }
        },
        estados_cargo: {
            get(){ return this.set_estados_cargo },
            set(nuevoValor){
                this.set_estados_cargo = nuevoValor
            }
        },
        is_disabled(){
            const set1 = new Set(this.usuario.estados_cargo);
            const set2 = new Set(this.estados_cargo);
            if (this.usuario.estados_cargo.length === this.estados_cargo.length && [...set1].every(element => set2.has(element))) { return true }
            return false
        }
    },
    methods:{
        async inicializarEstados() {
            await this.$store.dispatch('ubicacion/obtenerEstados')
        },
        cerrarDialogo(){
            this.dialogo = false
        },
        async asignarEstados(){
            const { error, mensaje, status } = await this.$store.dispatch('usuario/asignarEstadosUsuario', {
                id: this.usuario.id, estados_cargo: this.estados_cargo
            })
            this.$store.commit('alerta/addAlertas', { estado: status, mensaje: mensaje, error: error })
        }
    }
}
</script>
