<template>
    <v-row justify="center">
        <v-col cols="12" sm="2">
            <v-sheet color="grey lighten-3" class="pa-4">
                <h1><b>Peticiones</b></h1>
            </v-sheet>
            <v-list>
                <v-list-item-group>
                    <v-list-item @click="inicializarRecursosUsuario()">
                        <v-list-item-content>
                            <v-list-item-title>Peticiones por verificar</v-list-item-title>
                        </v-list-item-content>
                        <!-- <v-list-item-action>n</v-list-item-action> -->
                    </v-list-item>
                </v-list-item-group>
                <v-list-item-group>
                    <v-list-item @click="inicializarRecursos({modalidad_pago:'transferencia', validado: 'True'})">
                        <v-list-item-content>
                            <v-list-item-title>
                                Peticiones por facturar</v-list-item-title>
                        </v-list-item-content>
                    </v-list-item>
                    <!-- <v-list-item link @click="inicializarRecursos({modalidad_pago:'transferencia', validado: 'True'})">
                        <v-list-item-content >
                            <v-list-item-title>Transferencias</v-list-item-title>
                        </v-list-item-content>
                        <v-list-item-action>n</v-list-item-action>
                    </v-list-item>
                    <v-list-item link @click="inicializarRecursos({modalidad_pago:'cheque', validado: 'True'})">
                        <v-list-item-content >
                            <v-list-item-title>Cheques</v-list-item-title>
                        </v-list-item-content>
                        <v-list-item-action>n</v-list-item-action>
                    </v-list-item> -->
                </v-list-item-group>
                <v-list-item-group>
                    <v-list-item @click="inicializarRecursosFacturados({modalidad_pago:'transferencia', validado: 'True'})">
                        <v-list-item-content>
                            <v-list-item-title>Peticiones por comprobar</v-list-item-title>
                        </v-list-item-content>
                        <!-- <v-list-item-action>n</v-list-item-action> -->
                    </v-list-item>
                </v-list-item-group>
            </v-list>
        </v-col>
        <v-col cols="12" sm="8">
            <peticiones
                :atributos_input="atributos_input"
                :addHeaders="headers"
                :titulo="titiulo"
            />
        </v-col>
    </v-row>
</template>

<script>
import Peticiones from '../../components/Recurso/TablaSolicitudesRecursos.vue'

export default {
    data: () => ({
        headers:[],
        titiulo: '',
        atributos_input:{
            show_archivo_input: false, titulo: 'Adjuntar archivo PDF de comprobante', tipo: 'comprobante', formatos: '.pdf', is_multiple: false
        },
        seleccionado: 1
    }),
    components: {
        peticiones: Peticiones
    },
    computed:{
        usuario() { return this.$store.getters['MOD_autenticacion/obtenerIdUsuario'] },
    },
    created(){
        this.inicializarRecursosUsuario()
    },
    methods: {
        async inicializarRecursos({validado, modalidad_pago }) {
            this.atributos_input={
                show_archivo_input: true, titulo: 'Adjuntar Archivos de Factura (PDF/XML)', tipo: 'factura', formatos: '.pdf, .xml', is_multiple: false
            }
            this.titiulo = 'Proceso de Validación y Confirmación de Pagos en Solicitudes de Recursos'
            this.headers= [
                {text: 'Validado', value: 'validado', align: 'center'},
                {text: 'Pagado', value: 'pagado', align: 'center'},
                {text: 'Facturado', value: 'facturado', align: 'center'},
                {text: 'Reembolsado | Comprobado', value: 'RemOrCom', align: 'center'}
            ]
            const { mensaje, estado, error } = await this.$store.dispatch('recurso/recuperarRecursosUsuario', {
                validado: validado, usuario_cargo: this.usuario, modalidad_pago: modalidad_pago
            })
            if(error) {
                this.$store.commit('alerta/addAlertas', { estado: estado, mensaje: mensaje, error: error, })
            }
        },
        async inicializarRecursosFacturados({validado, modalidad_pago }) {
            this.atributos_input={
                show_archivo_input: true, titulo: 'Adjuntar Acuse', tipo: 'acuse', formatos: '.pdf', is_multiple: false
            }
            this.titiulo = 'Proceso de Validación y Confirmación de Pagos en Solicitudes de Recursos'
            this.headers= [
                {text: 'Validado', value: 'validado', align: 'center'},
                {text: 'Pagado', value: 'pagado', align: 'center'},
                {text: 'Facturado', value: 'facturado', align: 'center'},
                {text: 'Reembolsado | Comprobado', value: 'RemOrCom', align: 'center'}
            ]
            const { mensaje, estado, error } = await this.$store.dispatch('recurso/recuperarRecursosUsuario', {
                validado: validado, usuario_cargo: this.usuario, modalidad_pago: modalidad_pago
            })
            if(error) {
                this.$store.commit('alerta/addAlertas', { estado: estado, mensaje: mensaje, error: error, })
            }
        },
        async inicializarRecursosUsuario(){
            this.titiulo = 'Peticiones de pago de derechos por validar'
            this.headers = [
                {text: 'Validar e indicar forma de pago', value: 'actions', align: 'center'},
                // {text: 'Pagado', value: 'pagado', align: 'center'},
                // {text: 'Reembolsado | Comprobado', value: 'RemOrCom', align: 'center'}
            ]
            const { mensaje, estado, error } = await this.$store.dispatch('recurso/recuperarRecursosUsuario', {
                usuario_cargo: this.usuario,
                validado: 'False',
                modalidad_pago: ''
            });
            if (error) {
                this.$store.commit('alerta/addAlertas', { estado: estado, mensaje: mensaje, error: error, });
            }
        }
    }
}
</script>