<template>
    <listar-trabajos
        :objFiltado="objFiltado"
        :headers="headers"
        :objAsignar="{
            isAsignarBtn: true,
            redireccionar: 'asignar-trabajos-usuario',
        }"
        @eventAplicarFiltroAtributos="inicializarTrabajosPendientes"
    />
</template>

<script>
import ListarTrabajos from '../../components/trabajo/TablaTrabajos.vue';
export default {
    data: () => ({
        headers: [
            { value: 'proyecto', text: 'Proyecto' },
            { value: 'concepto', text: 'Concepto' },
            { value: 'status', text: 'Status' },
            { value: 'inmueble.cliente', text: 'Cliente' },
            { value: 'inmueble.nombre', text: 'Inmueble' },
            { value: 'inmueble', text: 'Ubicación del inmueble' },
            { value: 'inmueble.estado', text: 'Usuario a cargo' },
            { value: 'inmueble.municipio', text: 'Usuario en campo' },
            { value: 'creado', text: 'Creado' },
            { value: 'modificado', text: 'Modificado' },
        ],
        objFiltado: {
            isFiltadoBtn: true,
            objFiltroAtributo: [
                {text: 'Cliente', disabled: false, indice: 0}, {text:'Estado', indice: 1}, {text: "Fecha", indice: 2}
            ]
        }
    }),
    components: {
        'listar-trabajos': ListarTrabajos,
    },
    created(){ this.inicializarTrabajosPendientes({cliente: null, estado: null, fecha_menor: null, fecha_mayor: null}) },
    methods: {
        async inicializarTrabajosPendientes({cliente, estado, fecha_menor, fecha_mayor}) {
            this.$store.commit('trabajo/cleanTrabajos')
            const filtado = {
                client: cliente ? cliente.id : null,
                province: estado ? estado.id : null,
                menorIgualFecha: cliente || estado ? null : fecha_menor,
                mayorIgualFecha: cliente || estado ? null : fecha_mayor,
            }
            const { mensaje, error, estados } = await this.$store.dispatch('trabajo/obtenerTrabajosPendietes', filtado)
            if (error) {
                await this.$store.commit('alerta/addAlertas', { estado: estados, mensaje: mensaje, error: error })
            }
        }
    }
};
</script>
