<template>
  <v-main>
    <v-row justify="center">
      <v-col cols="12" sm="8">
        <v-stepper v-model="paso">
          <v-stepper-header>
            <v-stepper-step step="1">
              Paso 1 <br>
              Cliente
            </v-stepper-step>
            <v-divider />
            <v-stepper-step step="2">
              Paso 2 <br>
              Inmuebles
            </v-stepper-step>
          </v-stepper-header>
          <v-stepper-items>
            <v-stepper-content step="1">
              <formulario-leer-inmuebles :is_cliente="is_cliente" @eventoContinuar="setValidP2"/>
              <br>
              <v-btn block color="success" @click="paso=2" :disabled="!isValid_p2">Ir al paso 2</v-btn>
            </v-stepper-content>
  
            <v-stepper-content step="2">
              <v-btn block color="primary" @click="paso=1">Ir al paso 1</v-btn>
              <br>
              <v-card class="mx-auto" :loading="getCargando" :disabled="getCargando">
                <v-card-title class="grey lighten-2">
                  {{ cliente.nombre }}
                </v-card-title>
                <v-card-subtitle class="grey lighten-2">
                  {{inmuebles.length}} {{inmuebles.length === 1 ? 'Inmuebl': 'Inmuebles'}}
                </v-card-subtitle>
                <v-card-text class="pt-2">
                  <tabla-inmuebles
                    :cabecera="cabecera"
                    :inmuebles="inmuebles"
                    texto_sin_datos="Seleccione un archivo, para verificar los inmuebles."
                    :obj_grupo_btn_verificar_inmuebles="{ is_btn_borror_inmuebles_verificados: true, is_btn_verificar_inmuebles: true}"
                  />
                </v-card-text>
              </v-card>
              <!-- <v-btn block color="success" @click="paso=2">Ir al paso 1</v-btn> -->
            </v-stepper-content>
  
          </v-stepper-items>
        </v-stepper>
      </v-col>
      <v-col cols="12" sm="2">
        <v-card>
          <v-card-actions>
            <v-spacer></v-spacer>
            <v-btn @click="$router.push({name: 'inmuebles', params: { cliente: cliente.nombre } })" color="error">Cancelar</v-btn>
          </v-card-actions>
        </v-card>
      </v-col>
    </v-row>
  </v-main>
</template>

<script>
import FormularioCrearInmueblesArchivo from '../../components/Inmueble/FormularioCrearMultiplesInmuebles.vue'
import TablaCrearInmueblesArchivo from '../../components/Inmueble/TablaInmuebles'

export default {
  props:{
    is_cliente: { type: Boolean, default: true }
  },
  components: {
    'formulario-leer-inmuebles': FormularioCrearInmueblesArchivo,
    'tabla-inmuebles': TablaCrearInmueblesArchivo,
  },
  data: () => ({
    cabecera: [   
      { value: 'status', text: 'Status'},   
      { value: 'clave', text: 'Clave', sortable: false},
      { value: 'clave_2', text: 'Clave 2' },
      { value: 'nombre', text: 'Inmueble' },
      { value: 'estado', text: 'Estado' },
      { value: 'municipio', text: 'Municipio' },
      { value: 'localidad', text: 'Localidad' },
      { value: 'direccion', text: 'Direccion' },      
      { value: 'accion', text: 'Act. & Eli.', align: 'center' },
    ],
    paso: 1,
    isValid_p2: false
  }),
  computed:{
    cliente() { return this.$store.getters['cliente/getCliente'] },
    getCargando() { return this.$store.getters['inmueble/getCargando'] },
    inmuebles(){ return this.$store.getters['inmueble/getInmueblesVerificados'] }
  },
  methods:{
    setValidP2({validacion}){ this.isValid_p2 = validacion },
  }
};
</script>
