<template>
    <v-card flat>
        <listar-archivos-recurso />
        <v-card-text>
            <v-file-input
                v-model="archivoBase64"
                :label="atributos_input.titulo"
                dense
                outlined
                :accept="atributos_input.formatos"
                show-size
                :disabled="cargando"
                :loading="cargando"
                :multiple="true"
                clearable
            >
                <template v-slot:append-outer>
                    <v-btn icon @click="subirArchivo(archivoBase64)" :disabled="archivoBase64.length ? false : true">
                        <v-icon color="primary">mdi-cloud-upload</v-icon>
                    </v-btn>
                </template>
            </v-file-input>
        </v-card-text>
    </v-card>
</template>

<script>
import ListarArchivosRecurso from "../Archivo/ListarArchivos.vue"

export default {
    props: {
        atributos_input:{type: Object, default:()=>({titulo: 'Adjuntar archivo PDF de comprobante', tipo: 'comprobante', formatos: '.pdf', is_multiple: false})},
        recurso_id:{type: Number, default: 0},
        recurso_index:{type: Number, default: -1}
    },
    data:()=>({
        archivoBase64: []
    }),
    components: {
        "listar-archivos-recurso": ListarArchivosRecurso
    },
    computed:{
        cargando() { return this.$store.getters["recurso/getCargando"] },
        archivos() { return this.$store.getters["recurso/getArchivos"] },
        usuario() { return this.$store.getters["MOD_autenticacion/obtenerIdUsuario"] },
    },
    methods: {
        async subirArchivo(archivoBase64) {
            const { mensaje, estado, error } = await this.$store.dispatch("recurso/subirArchivo", {
                'archivos': archivoBase64, 'recurso': this.recurso_id, 'usuario': this.usuario
            });
            this.$store.commit("alerta/addAlertas", { 'estado': estado, 'mensaje': mensaje, 'error': error, });
            if(!error){
                this.clearArchivoBase64()
                const { mensaje, estado, error } = await this.$store.dispatch('recurso/confirmarValidarRecuruso', {
                    id: this.recurso_id, pagado:true, detalle_status: 'comprobacion', indice:this.recurso_index
                })
                this.$store.commit("alerta/addAlertas", { "estado": estado, "mensaje": mensaje, "error": error })
            }
        },
        clearArchivoBase64(){
            this.archivoBase64 = []
        }
    }
}
</script>