import axios from '../../api.js'

import { localStorage2json } from '@/utils/data/store'

const state = {
    contactos:[],

    contacto: {
        id: null,
        nombre: '',
        apellidos: '',
        numero_telefonico_1: '',
        numero_telefonico_2: '',
        correo_electronico: '',
        municipios: []
    },

    mensaje: '',

    is_cargando: false
}

const mutations = {
    setContactos(state, payload){ state.contactos = payload },
    addContacto(state, payload){ state.contactos.push(payload) },
    setContacto(state, payload){ state.contacto = payload },
    setCargando(state, payload){ state.is_cargando = payload },
    clearContacto(state){
        state.contacto= {
            id: null,
            nombre: '',
            apellidos: '',
            numero_telefonico_1: '',
            numero_telefonico_2: '',
            correo_electronico: '',
            municipios: []
        }
    }
}

const actions = {
    async recuperarContactos({commit}){
        try {
            const { data, status } = await axios.instance.get(`/usuarios/cantacto/`, {headers: {Authorization : `Bearer ${localStorage2json('token')}`}})
            const { items, message } = data
            var contactos = []
            for(const contacto of items){
                contactos.push({
                    id: contacto.id,
                    nombre: contacto.name,
                    apellidos: contacto.last_name,
                    correo_electronico: contacto.email,
                    numero_telefonico_1: contacto.phone_one,
                    numero_telefonico_2: contacto.phone_two,
                    municipios: contacto.municipalities
                })
            }
            commit('setContactos', contactos)
            return { 'mensaje': message, 'estado': status }
        } catch (error) {
            return { 'error': error }
        }
    },
    async crearNuevoContacto({commit}, {nombre, apellidos, correo_electronico, numero_telefonico_1, municipios}){
        try {
            commit('setCargando', true)
            const contact = {
                name:nombre,
                last_name: apellidos,
                phone_one: numero_telefonico_1,
                email: correo_electronico,
                municipalities: municipios
            }
            const { data, status } = await axios.instance.post('/usuarios/cantacto/', contact)
            const { message, items } = data
            const { id, name, last_name, phone_one, phone_two, email, municipalities } = items
            const contacto = {
                id: id === undefined ? null : id,
                nombre: name,
                apellidos: last_name,
                correo_electronico: email,
                numero_telefonico_1: phone_one,
                numero_telefonico_2: phone_two === undefined ? '' : phone_two,
                municipios: municipalities
            }
            commit('addContacto', contacto)
            commit('setCargando', false)
            return { mensaje: message, estado: status }
        } catch (error) {
            commit('setCargando', false)
            return { error: error }
        }
    }
}

const getters = {
    getContactos:(state) => state.contactos,
    getContacto:(state) => state.contacto,
}

export default {
    namespaced: true,
    state,
    mutations,
    actions,
    getters
}