<template>
    <v-dialog v-model="is_dialogo" persistent max-width="800">
        <template v-slot:activator="{ on, attrs }">
        <v-btn class="ma-2" color="success" dark v-bind="attrs" v-on="on">
            <v-icon dark left> mdi-contacts </v-icon> Agregar nuevo contacto 
        </v-btn>
        </template>
            <v-card class="mx-auto" :loading="is_cargando_mod_cliente" :disabled="is_cargando_mod_cliente">
                <v-card-title class="grey lighten-2">
                    Agregar nuevo contacto
                    <v-spacer></v-spacer>
                    <v-btn icon color="error" @click="cerrar()"><v-icon>mdi-close</v-icon></v-btn>
                </v-card-title>
                <v-card-text>
                    {{ contacto }}
                    <v-form class="container" ref="form" lazy-validation>
                        <v-row dense>
                            <v-col xs="12" sm="12" md="4" lg="4" xl="4">
                                <v-text-field
                                    v-model="contacto.nombre"
                                    type="text"
                                    :rules="[
                                        v => !!v || 'Se requiere el nombre',
                                        v => (v && v.length >= 3) || 'El nombre debe tener, como mínimo, tres caracteres'
                                    ]"
                                    label="Nombre*"
                                    outlined
                                    dense
                                    filled
                                />
                            </v-col>
                            <v-col xs="12" sm="12" md="4" lg="4" xl="4">
                                <v-text-field
                                    v-model="contacto.apellidos"
                                    label="Apellidos*"
                                    type="text"
                                    :rules="[
                                        v => !!v || 'Se requiere sus apellidos',
                                        v => (v && v.length >= 3) || 'El apellido debe tener, como mínimo, tres caracteres'
                                    ]"
                                    outlined
                                    dense
                                    filled
                                />
                            </v-col>
                            <v-col xs="12" sm="12" md="4" lg="4" xl="4">
                                <v-text-field
                                    v-model="contacto.correo_electronico"
                                    label="Correo*"
                                    type="email"
                                    :rules="[
                                        v => !!v || 'Se requiere de un correo electrónico',
                                        v => /.+@.+\..+/.test(v) || 'El correo electrónico debe ser válido.',
                                    ]"
                                    outlined
                                    dense
                                    filled
                                />
                            </v-col>
                            <v-col xs="12" sm="12" md="6" lg="6" xl="6">
                                <v-text-field
                                    v-model="contacto.numero_telefonico_1"
                                    label="Número de teléfono #1*"
                                    :rules="[
                                        v => !!v || 'Se requiere un número teléfono',
                                        v => (v && v.length >= 10) || 'El número debe contener al menos 10 dígitos.'
                                    ]"
                                    maxlength="15"
                                    type="number"
                                    outlined
                                    dense
                                    filled
                                ></v-text-field>
                            </v-col>
                            <v-col xs="12" sm="12" md="6" lg="6" xl="6">
                                <v-text-field
                                    v-model="contacto.numero_telefonico_2"
                                    label="Número de teléfono #2"
                                    maxlength="15"
                                    type="number"
                                    outlined
                                    dense
                                    filled
                                ></v-text-field>
                            </v-col>
                            <v-col xs="12" sm="12" md="6" lg="6" xl="6">
                                <v-select
                                        v-model="estado"
                                        label="Estado"
                                        outlined
                                        :items="estados"
                                        :disabled="isEstados"
                                        item-text="nombre"
                                        return-object
                                        :loading="isCargandoEstados"
                                        clearable
                                        dense
                                        filled
                                    />
                                </v-col>
                                <v-col xs="12" sm="12" md="6" lg="6" xl="6">
                                    <v-select
                                        v-model="contacto.municipios"
                                        label="Municipio"
                                        :items="municipios"
                                        item-text="nombre"
                                        item-value="id"
                                        :rules="[v => !!v || 'Item is required']"
                                        outlined
                                        dense
                                        filled
                                        multiple
                                        chips
                                        :loading="isCargandoMunuicipios"
                                        :disabled="isMunicipios"
                                        clearable
                                    />
                            </v-col>
                        </v-row>
                    </v-form>
                </v-card-text>
                <v-card-actions>
                    <v-spacer />
                    <v-btn @click="cerrar()" color="red" class="ma-3" dark>Cerrar</v-btn>
                    <v-btn @click="crearNuevoContacto()" color="green" dark>Agregar</v-btn>
                </v-card-actions>
            </v-card>
    </v-dialog>
</template>

<script>
export default {
    data: () => ({
        dialog: false,
    }),
    computed: {
        is_dialogo:{
            get(){ return this.dialog },
            set(nuevoValor){
                this.dialog = nuevoValor
                if(nuevoValor){ this.inicializarEstados() }
            }
        },
        contacto(){ return this.$store.getters["contacto/getContacto"] },
        is_cargando_mod_cliente() { return this.$store.getters["contacto/getCargando"] },
        estados() { return this.$store.getters["ubicacion/getEstados"]; },
        estado: {
            get() { return this.$store.getters["ubicacion/getEstado"]; },
            async set(nuevoValor) {
                if (nuevoValor) {
                    await this.$store.dispatch("ubicacion/obtenerEstado", nuevoValor);
                    await this.obtenerMunicipiosEstado();
                } else {
                    this.$store.commit("ubicacion/clearEstado")
                    this.$store.commit("ubicacion/clearAllMunicipio")
                }
            },
        },
        isEstados() { return this.estados.length ? false : true; },
        isCargandoEstados() { return this.$store.getters["ubicacion/getCargandoEstados"]; },
        municipios() { return this.$store.getters["ubicacion/getMunicipios"]; },
        municipio: {
            get() { return this.$store.getters["ubicacion/getMunicipio"]; },
            async set(nuevoValor) {
                if(nuevoValor){
                    await this.$store.dispatch("ubicacion/obtenerMunicipio", nuevoValor);
                } else {
                    this.$store.commit("ubicacion/clearMunicipio")
                }
            },
        },
        isMunicipios() { return this.municipios.length ? false : true; },
        isCargandoMunuicipios() { return this.$store.getters["ubicacion/getCargandoMunicipios"]; },
    },
    methods: {
        async crearNuevoContacto() {
            if (this.$refs.form.validate()) {
                const { mensaje, estado, error } = await this.$store.dispatch("contacto/crearNuevoContacto", this.contacto);
                this.$store.commit("alerta/addAlertas", { "estado": estado, "mensaje": mensaje, "error": error })
                if(!error){ this.cerrar() }
            }
        },
        cerrar() {
            this.$refs.form.reset();
            this.$refs.form.resetValidation();
            this.dialog = false;
        },
        async inicializarEstados() {
            await this.$store.dispatch("ubicacion/obtenerEstadosDeInmueblesExistentes", {cliente: null});
        },
        async obtenerMunicipiosEstado() {
            const { estado } = this
            await this.$store.dispatch(
                "ubicacion/obtenerMunicipiosDeInmueblesExistentes", { cliente: null, estado }
            );
        },
    },
};
</script>
