<template>
    <v-card outlined class="mx-auto">
        <v-card-title class="grey lighten-3">
        Trabajos
        <v-spacer></v-spacer>
        <v-text-field
            v-model="search"
            append-icon="mdi-magnify"
            label="Buscar"
            single-line
            hide-details
            clearable
            dense
            filled
            outlined
            rounded
            class="white"
        />
        </v-card-title>
        <v-card-subtitle class="grey lighten-3"> {{ trabajos.length + (trabajos.length === 1 ? " Trabajo" : " Trabajos") }} </v-card-subtitle>
        <v-card-text>
            <v-responsive class="overflow-y-auto" max-height="500">
                <v-data-table
                    v-model="trabajos_seleccionados"
                    class="elevation-5"
                    :loading="isCargando"
                    :headers="headers"
                    :items="trabajos"
                    :items-per-page="-1"
                    loading-text="Cargando... por favor espere"
                    :footer-props="{
                        itemsPerPageText: 'Filas por página',
                        itemsPerPageAllText: 'Todos',
                        pageText: 'p - Tp de n',
                    }"
                    :search="search"
                    :show-select="mostrar_seleccionados"
                    :single-select="singleSelect"
                >
                    <template v-slot:top>
                        <v-toolbar flat>
                            <v-spacer />
                            <v-btn v-if="is_peticion"
                                :disabled="isTrabajos"
                                class="ma-5 primary"
                                @click="redireccionarFormularioPeticionPagoDerechos()">
                                Petición de pago de derechos
                            </v-btn>
                            <v-btn v-if="objAsignar.isAsignarBtn"
                                @click="redirecionar()"
                                :disabled="objAsignar.deshabilitar"
                                class="success ma-1"
                            >
                                Asignar trabajo
                                <v-icon right>mdi-file-account</v-icon>
                            </v-btn>
                            <filtrado-trabajos  v-if="objFiltado.isFiltadoBtn"
                                :obj_atributo_filtro="objFiltado.objFiltroAtributo"
                                @eventAplicarFiltroAtributos="emitAplicarFiltroAtributos"
                            />
                        </v-toolbar>
                    </template>
                    <template #no-data>
                        <span>{{ mensaje }}</span>
                    </template>
                    <!-- <template v-slot:item.data-table-select="{item, isSelected, select}">
                        <v-simple-checkbox
                            :value="isSelected"
                            :readonly="item.in_solicitud_recursos"
                            :disabled="item.in_solicitud_recursos"
                            @input="select($event)"
                        ></v-simple-checkbox>
                    </template> -->
                    <template v-slot:item.inmueble.nombre="{item}">
                        <span>{{item.inmueble.nombre}} (<b>{{item.inmueble.clave}})</b> </span>
                    </template>
                    <template v-slot:item.inmueble="{item}">
                        <span> <b>{{item.inmueble.estado.name}}</b><br>{{item.inmueble.municipio.name}}</span>
                    </template>
                    <template v-slot:item.inmueble.estado="{item}">
                        <div v-if="item.inmueble.estado.users_charge.length">
                            <v-chip v-for="{charge} in item.inmueble.estado.users_charge" :key="charge" class="ma-1" color="primary" label small>
                                <v-icon left> mdi-account-circle-outline </v-icon>
                                {{charge}}
                            </v-chip>
                        </div>
                        <div v-else> <v-chip label color="warning" small>No asignado</v-chip></div>
                    </template>
                    <template v-slot:item.inmueble.municipio="{item}">
                        <div v-if="item.inmueble.municipio.users_field.length">
                            <v-chip v-for="{name} in item.inmueble.municipio.users_field" :key="name" class="ma-1" color="primary" label small>
                                <v-icon left> mdi-account-circle-outline </v-icon>
                                {{name}}
                            </v-chip>
                        </div>
                        <div v-else> <v-chip label color="warning" small>No asignado</v-chip></div>
                    </template>
                    <template v-slot:item.creado="{ item }">
                        {{ getFecha(item.creado)}}
                    </template>
                    <template v-slot:item.modificado="{ item }">
                        <span :class="getColor(item.modificado) + ' lighten-3 '" style="display: flex; text-align: center">{{ getFecha(item.modificado)}}</span>
                    </template>
                    <template v-slot:item.property_office="{item}">
                        {{ item.property_office ? item.property_office.name : "" }} ({{ item.property_office ? item.property_office.property_key : "" }})
                    </template>
                </v-data-table>
            </v-responsive>
        </v-card-text>
  </v-card>
</template>

<script>
import FiltradoTrabajos from '../../components/trabajo/Filtrado.vue'
export default {
    props: {
        objFiltado: { type: Object, default:()=>({ isFiltadoBtn: false, objFiltroAtributo: [] }) },
        objAsignar: { type: Object, default:() => ({isAsignarBtn: false, deshabilitar: false, redireccionar: ''}) },
        mostrar_seleccionados: { type: Boolean, default: false },
        is_peticion: { type: Boolean, default: false },
        headers: { type: Array, default: () => {} }
    },
    data: () => ({
        search: '',
        blue: 3,
        singleSelect: false
    }),
    components:{
        'filtrado-trabajos': FiltradoTrabajos
    },
    computed: {
        trabajos() { return this.$store.getters['trabajo/getTrabajos'] },
        isCargando() { return this.$store.getters['trabajo/getCargando'] },
        isTrabajos() { return this.trabajos.length ? false : true },
        trabajos_seleccionados: {
            get(){ return this.$store.getters['trabajo/getTrabajosSeleccionados'] },
            set(nuevoValor){
                const peticiones = []
                for(const trabajos_seleccionado of nuevoValor)
                    peticiones.push({'trabajo': trabajos_seleccionado.id})
                this.$store.commit('trabajo/setSeleccionados', nuevoValor)
                this.$store.commit('recurso/setVerificarPeticiones', peticiones)
                this.$store.dispatch('recurso/updatePeticionesVerificadas')
            }
        },
        mensaje() { return this.$store.getters['trabajo/getMensaje'] }
    },
    methods: {
        holgura(modified_date) {
            var date1 = new Date(modified_date)
            var date2 = new Date()
            var datestring = date2.getFullYear() + '-' + (date2.getMonth() + 1) + '-' + date2.getDate()
            var date3 = new Date(datestring)
            var holgura = (date3.valueOf() - date1.valueOf()) / 86400000
            if (this.blue < holgura) this.blue = holgura
            return holgura
        },
        getColor(modified_date) {
            if (this.holgura(modified_date) >= 5) return 'red'
            else if (this.holgura(modified_date) >= 2) return 'orange'
            else return 'green'
        },
        getFecha(fecha){
            const fecha_es = new Date(fecha)
            fecha_es.setDate(fecha_es.getDate() + 1)
            const formato = { weekday: 'short', year: 'numeric', month: 'long', day: 'numeric' }
            return fecha_es.toLocaleDateString('es-Mx', formato)
        },
        verTrabajo(value) {
            this.$store.commit('trabajo/setTrabajo', value)
            this.$router.push({
                name: 'De',
            })
        },
        redirecionar(){
            this.$router.push({name: this.objAsignar.redireccionar, params: {nombreUsuario: 'fernando', sinClienteSeleccionado: true}})
        },
        emitAplicarFiltroAtributos(event){
            this.$emit('eventAplicarFiltroAtributos', event)
        },
        redireccionarFormularioPeticionPagoDerechos(){
            this.$router.push({ name: 'peticion-recursos' })
            this.$store.commit('trabajo/cleanTrabajos')
        }
    },
};
</script>
