<template>
    <v-row no-gutters justify="center">
      <v-col cols="12" sm="8" md="8" lg="8" xl="8" xs="8">
        <v-card class="mx-auto" outlined elevation="5">
          <tabla-contactos
            :contactos="contactos"
            :cabeceras="cabeceras"
            @actualizarListaUsuario="inicializarContactos"
          />
        </v-card>
      </v-col>
    </v-row>
  </template>
  
  <script>
  import TablaContactos from '../../components/Contacto/TablaContacto.vue'
  export default {
    data: () => ({
      cabeceras: [
        { text: 'Correo', value: 'correo_electronico' },
        { text: 'Nombre', value: 'nombre' },
        { text: 'Número de teléfono #1', value: 'numero_telefonico_1', align:'center' },
        { text: 'Número de teléfono #2', value: 'numero_telefonico_2', align:'center' },
        { text: 'Número de municipios asignados.', value: 'accion', align: 'center' }
      ]
    }),
    components: {
      "tabla-contactos": TablaContactos
    },
    computed: {
      contactos() { return this.$store.getters["contacto/getContactos"] }
    },
    created(){
      this.inicializarContactos()
    },
    methods:{
      async inicializarContactos(){
        const { mensaje, estado, error } = await this.$store.dispatch("contacto/recuperarContactos")
        this.$store.commit("alerta/addAlertas", { "estado": estado, "mensaje": mensaje, "error": error })
      }
    }
  }
  </script>