<template>
    <v-row justify="center">
        <v-col cols="12" sm="2">
            <v-sheet color="grey lighten-4" class="pa-4">
                <h1><b>Pagos</b></h1>
            </v-sheet>
            <v-list>
                <v-list-group no-action>
                    <template v-slot:activator>
                        <v-list-item-content>
                            <v-list-item-title>Movimientos pendientes</v-list-item-title>
                        </v-list-item-content>
                    </template>
                    <v-list-item link>
                        <v-list-item-content @click="inicializarRecursos({modalidad_pago: 'transferencia', validado: 'True', pagado: 'False'})">
                            <v-list-item-title>Transferencia</v-list-item-title>
                        </v-list-item-content>
                    </v-list-item>
                    <v-list-item link>
                        <v-list-item-content @click="inicializarRecursos({modalidad_pago: 'cheque', validado: 'True', pagado: 'False'})">
                            <v-list-item-title>Cheque</v-list-item-title>
                        </v-list-item-content>
                    </v-list-item>
                </v-list-group>
                <v-list-group no-action>
                    <template v-slot:activator>
                        <v-list-item-content>
                            <v-list-item-title>Movimientos aprobados</v-list-item-title>
                        </v-list-item-content>
                    </template>
                    <v-list-item link>
                        <v-list-item-content @click="inicializarRecursos({modalidad_pago: 'transferencia', validado: 'True', pagado: 'True'})">
                            <v-list-item-title>Reembolso</v-list-item-title>
                        </v-list-item-content>
                    </v-list-item>
                    <v-list-item link>
                        <v-list-item-content @click="inicializarRecursos({modalidad_pago: 'cheque', validado: 'True', pagado: 'True'})">
                            <v-list-item-title>Comprobacion</v-list-item-title>
                        </v-list-item-content>
                    </v-list-item>
                </v-list-group>
            </v-list>
        </v-col>
        <v-col cols="12" sm="8">
            <peticiones :atributos_input="atributos_input"/>
        </v-col>
    </v-row>
</template>

<script>
import Peticiones from "../../components/Recurso/TablaSolicitudesRecursos.vue";

export default {
    data:()=>({
        atributos_input:{
            show_archivo_input: true,
            titulo: 'Adjuntar archivo PDF de comprobante',
            tipo: 'comprobante',
            formatos: '.pdf',
            is_multiple: false
        },
    }),
    components: {
        peticiones: Peticiones
    },
    created(){
        this.inicializarRecursos({modalidad_pago: "transferencia", validado: 'True', pagado: 'False'})
    },
    methods: {
        async inicializarRecursos({validado, pagado, modalidad_pago}) {
            const { mensaje, estado, error } = await this.$store.dispatch("recurso/recuperarRecursos", {
                modalidad_pago: modalidad_pago, validado: validado, pagado: pagado
            });
            if (error) {
                this.$store.commit("alerta/addAlertas", {estado: estado, mensaje: mensaje, error: error})
            }
            this.atributos_input.show_archivo_input = pagado === 'True' ? false: true
        }
    }
};
</script>