<template>
    <v-row dense justify="center">
        <v-col cols="12" sm="8">
            <tabla-trabajos-usuario
                :is_peticion="true"
                :headers="headers"
                :objAsignar="{isAsignarBtn: true, deshabilitar: false, redireccionar: 'asignar-trabajos'}"
            />
        </v-col>
    </v-row>
</template>

<script>
import TablaTrabajos from '../../components/trabajo/TablaTrabajos.vue'
export default {
    data:()=>({
        headers: [
            { text: 'Proyecto', value: 'proyecto' },
            { text: 'Concepto', value: 'concepto' },
            { text: 'Status', value: 'status' },
            { text: 'Inmueble', value: 'inmueble.nombre' },
            { value: 'inmueble', text: 'Ubicación del inmueble' },
            { value: 'inmueble.municipio', text: 'Usuario en campo' },
            { text: 'Creado', value: 'creado' },
            { text: 'Actualizado', value: 'modificado' },
        ],
    }),
    components: {
        'tabla-trabajos-usuario': TablaTrabajos,
    },
    created() { this.inicializarTrabajos({usuario_campo: null, usuario_cargo: this.username}) },
    computed: {
        username() { return this.$store.getters['MOD_autenticacion/obtenerIdUsuario'] },
    },
    methods: {
        async inicializarTrabajos({usuario_campo, usuario_cargo}) {
            this.$store.commit('trabajo/cleanTrabajos')
            const { mensaje, estado, error } = await this.$store.dispatch( 'trabajo/obtenerTrabajosUsuario',{usuario_campo:usuario_campo, usuario_cargo:usuario_cargo})
            if (error) {
                this.$store.commit('alerta/addAlertas', { 'estado': estado, 'mensaje': mensaje, 'error': error })
            }
        },
    },
};
</script>

<style>
    span {
        text-transform: none !important;
    }
    tbody tr:nth-of-type(odd) {
        background-color: rgba(76, 129, 253, 0.125);
    }
</style>
